.footer__text {
    max-width: 318px;
    font-family: "Inter", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
    margin: 0;
}

@media (max-width: 1024px) {
    .footer__text {
        max-width: 282px;
        font-size: 14px;
        line-height: 17px;
        padding-left: 19px;
    }
}
